// Page that displays configuration options
// and lets a list of Trackables be set up

import React, { useEffect, useState } from 'react';
import { ITrackable, Trackable, TrackableDB } from '../data/DBTrackables';
import './SetupPage.scss';

const SetupPage = () => {
    const [trackables, setTrackables] = useState<Trackable[]>([]);
    const [trackableField, setTrackableField] = useState<string>('');

    const db = new TrackableDB();

    useEffect(() => {
        setTrackables(db.getAll());
    }, []);

    const updateTrackableField = (ev) => {
        setTrackableField(ev.target.value);
    };

    const submitNewTrackable = (ev) => {
        ev.preventDefault();
        if (trackableField.length > 0) {
            db.add({ name: trackableField });
            setTrackables(db.getAll());
            setTrackableField('');
        }
    };

    const deleteTrackable = (trackable: ITrackable) => {
        return () => {
            db.remove(trackable);
            setTrackables(db.getAll());
        };
    };

    return (
        <div className="setup-page">
            <div className="container">
                <div className="card trackables-input">
                    <h3>Add New Trackable</h3>
                    <p>
                        A Trackable is a 'thing' to collect data on, i.e.:
                        "Tiredness" or "Anxiety".
                    </p>
                    <form action="#" onSubmit={submitNewTrackable}>
                        <input
                            type="text"
                            className="trackable-text-input"
                            placeholder="Feeling to track..."
                            value={trackableField}
                            onChange={updateTrackableField}
                        ></input>
                        <input
                            type="submit"
                            className="trackable-submit"
                            value="Add Item"
                            onClick={submitNewTrackable}
                        />
                    </form>
                </div>
                <div className="card trackables">
                    <h3>List of Trackables</h3>
                    {trackables.map((trackable) => {
                        return (
                            <div key={trackable.id} className="row">
                                {trackable.name}
                                <button
                                    className="trackable-delete"
                                    onClick={deleteTrackable(trackable)}
                                >
                                    
                                </button>
                            </div>
                        );
                    })}
                </div>
            </div>
        </div>
    );
};

export default SetupPage;
