import React, { useState } from 'react';
import {
    BrowserRouter,
    Route,
    Switch,
    useHistory,
    useLocation,
} from 'react-router-dom';
import NavBar from '../components/navbar/NavBar';
import CheckInPage from './CheckInPage';
import DashboardPage from './DashboardPage';
import './HomePage.scss';
import SetupPage from './SetupPage';

const HomePage = () => {
    const history = useHistory();

    const NavTo = (path: '/' | '/checkin' | '/trackables') => {
        return () => {
            history.push(path);
        };
    };

    return (
        <div className="homepage">
            <NavBar>
                <NavBar.Button onClick={NavTo('/')}>Reports</NavBar.Button>
                <NavBar.Button onClick={NavTo('/checkin')}>
                    Check-In
                </NavBar.Button>
                <NavBar.Button onClick={NavTo('/trackables')}></NavBar.Button>
            </NavBar>

            <Switch>
                <Route exact path="/">
                    <DashboardPage />
                </Route>
                <Route exact path="/checkin">
                    <CheckInPage />
                </Route>
                <Route exact path="/trackables">
                    <SetupPage />
                </Route>
            </Switch>
        </div>
    );
};

export default HomePage;
