// Tracks actual data points as responses, with attached
// date and time information.
// Supports fetching a range (i.e.: One day, a year, etc)

import { DateTime } from 'luxon/src/luxon';
import LocalDB from './LocalDB';

export interface DataPoint {
    id: number;
    value: number;
    min: number;
}

export interface DataSubmission {
    id?: number;
    responses: DataPoint[];
    note: string;
    date: string;
}

class ResponseDB {
    private _db: LocalDB = null;

    constructor() {
        this._db = new LocalDB('responses');
    }

    Get(id: number): DataSubmission {
        return this._db.GetByID(id) as DataSubmission;
    }

    GetAll(crunchSameDayResults: boolean = true): DataSubmission[] {
        const data: DataSubmission[] = this._db.Get() as DataSubmission[];
        // In the case of multiple check-ins on the same day, this
        // "crunches" them down into single data points, taking the
        // highest result for the day. Can be skipped by disabling crunch.
        const isSameDate = (date1: any, date2: any) => {
            const ldate1 = DateTime.fromISO(date1);
            const ldate2 = DateTime.fromISO(date2);

            return (
                ldate1.day === ldate2.day &&
                ldate1.month === ldate2.month &&
                ldate1.year === ldate2.year
            );
        };

        if (crunchSameDayResults) {
            let combinedData: DataSubmission[] = [];
            data.forEach((source, i) => {
                // Check if it exists in combinedData already
                let alreadyExists: boolean = false;

                for (let j = 0; j < combinedData.length; j++) {
                    const dest = combinedData[j];

                    if (isSameDate(source.date, dest.date)) {
                        // dest is already same data point
                        alreadyExists = true;

                        for (let x = 0; x < dest.responses.length; x++) {
                            const si = source.responses.findIndex(
                                (tid) => tid.id === dest.responses[x].id
                            );
                            if (si >= 0) {
                                let v1 = dest.responses[x].value;
                                let v2 = source.responses[si].value;

                                dest.responses[x].value = Math.max(
                                    dest.responses[x].value,
                                    source.responses[si].value
                                );

                                dest.responses[x].min = Math.min(
                                    dest.responses[x].value,
                                    source.responses[si].value
                                );
                            }
                        }

                        break;
                    }
                }

                if (!alreadyExists) {
                    combinedData.push(source);
                }
            });
            return combinedData;
        } else {
            return data;
        }
    }

    GetByDateRange(start: DateTime, end: DateTime, crunchData: boolean = true) {
        return this.GetAll(crunchData).filter((x) => {
            const date = DateTime.fromISO(x.date);
            return date >= start && date <= end;
        });
    }

    Add(submission: DataSubmission): number {
        let res = this._db.Insert(submission);
        return res?.id;
    }

    Remove(submission: DataSubmission): boolean {
        return this._db.Delete(submission.id);
    }

    Save(submission: DataSubmission): boolean {
        if (submission.id) {
            let row = {
                ...submission,
                id: submission.id,
            };

            return this._db.Update(row);
        } else {
            // Doesn't exist, add a new one
            return this.Add(submission) >= 0;
        }
    }
}

export default ResponseDB;
