import { DateTime } from 'luxon';
import React, { useEffect, useState } from 'react';
import { useHistory } from 'react-router';
import ResponseDB, { DataPoint, DataSubmission } from '../../data/DBResponses';
import { Trackable, TrackableDB } from '../../data/DBTrackables';
import HeatSelector from '../heatselector/HeatSelector';
import './CheckInForm.scss';

const CheckInForm = () => {
    const [emotion, setEmotion] = useState(5);
    const [trackables, setTrackables] = useState<Trackable[]>([]);
    const [responses, setResponses] = useState<DataPoint[]>([]);
    const [note, setNote] = useState<string>('');
    const trackableDB = new TrackableDB();
    const responseDB = new ResponseDB();
    const history = useHistory();

    useEffect(() => {
        const newTrackables = trackableDB.getAll();
        let responses = [];
        newTrackables.forEach((t) => {
            responses.push({
                id: t.id,
                value: 5,
            });
        });
        setTrackables(newTrackables);
        setResponses(responses);
    }, []);

    const onResponseChange = (index: number) => {
        return (value: number) => {
            responses[index].value = value;
            setResponses(responses);
        };
    };

    const onSubmit = () => {
        // Create new response
        const data: DataSubmission = {
            date: DateTime.now().toISO(),
            note,
            responses,
        };

        // Save to DB
        responseDB.Add(data);
        history.push('/');
    };

    const handleNoteChange = (ev) => {
        setNote(ev.target.value);
        console.log(note);
    };

    return (
        <div className="check-in--form">
            <h2>Check-In Form</h2>
            <h4>How are you feeling?</h4>

            {trackables.map((trackable, index) => {
                return (
                    <span className="check-card" key={trackable.id}>
                        <p>
                            On a scale of 1 to 10, how{' '}
                            <b>
                                <em>{trackable.name}</em>
                            </b>{' '}
                            are you feeling right now?
                        </p>
                        <HeatSelector onChange={onResponseChange(index)} />
                    </span>
                );
            })}

            <p>Enter a note if you want to:</p>
            <textarea
                className="check-in-note-input"
                placeholder="Note..."
                value={note}
                onChange={handleNoteChange}
                rows={5}
            />
            <p>Submit when you're done!</p>
            <button className="check-in-submit" onClick={onSubmit}>
                Submit
            </button>
        </div>
    );
};

export default CheckInForm;
