import { DateTime } from 'luxon';
import React, { useEffect, useState } from 'react';
import {
    CartesianGrid,
    Legend,
    Line,
    LineChart,
    PolarAngleAxis,
    PolarGrid,
    PolarRadiusAxis,
    Radar,
    RadarChart,
    ResponsiveContainer,
    Tooltip,
    XAxis,
    YAxis,
} from 'recharts';
import ResponseDB, { DataSubmission } from '../data/DBResponses';
import { ITrackable, TrackableDB } from '../data/DBTrackables';
import './DashboardPage.scss';

const DashboardPage = () => {
    const [data, setData] = useState<DataSubmission[]>([]);
    const [dataAll, setDataAll] = useState<DataSubmission[]>([]);
    const [trackables, setTrackables] = useState<ITrackable[]>([]);
    const db = new ResponseDB();
    const trackableDB = new TrackableDB();
    const [showBreakdown, setShowBreakdown] = useState<boolean>(false);

    useEffect(() => {
        const today = DateTime.now();
        const todayStart = DateTime.local(today.year, today.month, today.day);
        const todayEnd = DateTime.local(
            today.year,
            today.month,
            today.day,
            23,
            59,
            59,
            999
        );
        setData(db.GetByDateRange(todayStart, todayEnd));
        setDataAll(db.GetByDateRange(todayStart, todayEnd, false));
        //setData(db.GetAll());
    }, []);

    useEffect(() => {
        setTrackables(trackableDB.getAll());
    }, [data]);

    let todaysData = [];
    data.forEach((d) => {
        todaysData = d.responses;
        todaysData.forEach((point) => {
            point.name = trackableDB.get(point.id).name;
        });
    });

    let todaysDataBreakdown = [];
    let trackableColors = {};

    /// TODO: Break this into its own utility file
    const hashToColorString = (str: string) => {
        let hash = 0;
        for (let i = 0; i < str.length; i++) {
            hash = str.charCodeAt(i) + ((hash << 5) - hash);
        }
        let colorString = '#';
        for (let i = 0; i < 3; i++) {
            const val = (hash >> (i * 8)) * 0xff;
            colorString += ('00' + val.toString(16)).substr(-2);
        }
        return colorString;
    };

    dataAll.forEach((d) => {
        let date = DateTime.fromISO(d.date);
        let res = {
            time: `${date.hour}:${date.minute}`,
        };

        d.responses.forEach((v) => {
            const tname = trackableDB.get(v.id).name;

            res[tname] = v.value;
        });

        todaysDataBreakdown.push(res);
    });

    let averages = {};

    todaysDataBreakdown.forEach((d) => {
        trackables.forEach((trackable) => {
            averages[trackable.name] = averages[trackable.name]
                ? averages[trackable.name] + d[trackable.name]
                : 0 + d[trackable.name];
        });
    });

    let averageData = [];
    trackables.forEach((trackable) => {
        trackableColors[trackable.name] = hashToColorString(trackable.name);

        averages[trackable.name] /= todaysDataBreakdown.length;
        averages[trackable.name] = Math.ceil(averages[trackable.name]);

        todaysData.forEach((daydata) => {
            if (daydata.name === trackable.name) {
                daydata.average = averages[trackable.name];
            }
        });
    });

    const toggleShowBreakdown = (ev) => {
        setShowBreakdown(ev.target.checked);
    };

    return (
        <div className="dashboard">
            <div
                style={{
                    display: 'grid',
                    gridTemplateRows: 'auto 1fr',
                    gridTemplateColumns: '1fr',
                    width: '100%',
                    height: '100%',
                    placeItems: 'center',
                }}
            >
                <div style={{ textAlign: 'center' }}>
                    <h2>Daily Stats</h2>

                    <span>
                        <input
                            type="checkbox"
                            onChange={toggleShowBreakdown}
                            checked={showBreakdown}
                        />
                        <label>Show full breakdown for day</label>
                    </span>
                </div>
                {showBreakdown === false && (
                    <ResponsiveContainer
                        className="dashboard-graph"
                        width="75%"
                        height="75%"
                    >
                        <RadarChart data={todaysData}>
                            <PolarGrid />
                            <PolarAngleAxis dataKey="name" />
                            <PolarRadiusAxis angle={90} domain={[-1, 10]} />

                            <Radar
                                name="Peak"
                                dataKey="value"
                                stroke="#ca829d"
                                fill="#ca829d"
                                fillOpacity={0.6}
                            />
                            <Radar
                                name="Average"
                                dataKey="average"
                                stroke="#35Aa5d"
                                fill="#55ca9d"
                                fillOpacity={0.6}
                            />
                            <Radar
                                name="Minimum"
                                dataKey="min"
                                stroke="#355DAA"
                                fill="#559DCA"
                                fillOpacity={0.6}
                            />
                            <Legend />
                        </RadarChart>
                    </ResponsiveContainer>
                )}

                {showBreakdown === true && (
                    <ResponsiveContainer
                        className="dashboard-graph"
                        width="75%"
                        height="75%"
                    >
                        <LineChart data={todaysDataBreakdown}>
                            <CartesianGrid strokeDasharray="1 1" />
                            <XAxis dataKey="time" />
                            <YAxis />
                            <Tooltip />
                            <Legend iconSize={7} iconType="diamond" />
                            {trackables.map((trackable, index) => {
                                return (
                                    <Line
                                        key={index}
                                        dataKey={trackable.name}
                                        stroke={trackableColors[trackable.name]}
                                    />
                                );
                            })}
                        </LineChart>
                    </ResponsiveContainer>
                )}
            </div>
        </div>
    );
};

export default DashboardPage;
