// Local DB - uses LocalStorage to create a basic store
// that supports simple Create/Read/Update/Delete functionality

/// Specifies what a minimal database row needs in order to be
/// added to the DB. Presently, just an ID field.
interface DBRow {
    id: number;
    isDeleted?: boolean;
}

type DBStorageObjectType = {
    rows: DBRow[];
    idgen: number;
};

class LocalDB {
    private dbIdentifier = 'default';

    private storageObject: DBStorageObjectType = {
        rows: [],
        idgen: 1,
    };

    public get Identifier() {
        return this.dbIdentifier;
    }

    /// Load Table with the given identifier string, (i.e.: "restaurants")
    /// and prepare it for use.
    constructor(identifier: string) {
        this.dbIdentifier = identifier;
        this.ReadDB(true);
    }

    // ==-- Public Call API --==

    /// Get all entries
    Get(): DBRow[] {
        this.ReadDB();
        return this.storageObject.rows.filter((x) => !x.isDeleted);
    }

    GetByID(id: number): DBRow {
        this.ReadDB();
        return this.storageObject.rows.find((x) => x.id === id);
    }

    Insert(row: any): DBRow {
        this.ReadDB();
        const id = this.GetNewId();
        const dbRow = {
            id,
            ...row,
        };

        this.storageObject.rows.push(dbRow);
        this.WriteDB();

        return dbRow;
    }

    Update(row: DBRow): boolean {
        this.ReadDB();
        const index = this.storageObject.rows.findIndex((x) => x.id === row.id);
        if (index >= 0) {
            this.storageObject.rows[index] = row;
            this.WriteDB();
        } else {
            // Throw exception instead?
            return false;
        }

        return true;
    }

    UnDelete(id: number): boolean {
        this.ReadDB();
        const index = this.storageObject.rows.findIndex((x) => x.id === id);

        if (index >= 0) {
            this.storageObject.rows[index].isDeleted = undefined;
            this.WriteDB();
            return true;
        }

        return false;
    }

    Delete(id: number, hardDelete: boolean = false): boolean {
        this.ReadDB();
        const index = this.storageObject.rows.findIndex((x) => x.id === id);
        if (index >= 0) {
            if (hardDelete) {
                this.storageObject.rows.splice(index, 1);
            } else {
                this.storageObject.rows[index].isDeleted = true;
            }
            this.WriteDB();
        } else {
            return false;
        }

        return true;
    }

    // ==-- Internal Calls --==
    /// Re-reads DB from LocalStorage to refresh it prior to performing an action.
    private ReadDB(createIfNull: boolean = false): void {
        const strStorageObject = localStorage.getItem(this.Identifier);
        if (strStorageObject !== null) {
            this.storageObject = JSON.parse(strStorageObject);
        } else {
            if (createIfNull) {
                const json = JSON.stringify(this.storageObject);
                localStorage.setItem(this.Identifier, json);
            }
        }
    }

    /// Writes current object store to DB
    private WriteDB(): void {
        const jsonString = JSON.stringify(this.storageObject);
        localStorage.setItem(this.Identifier, jsonString);
    }

    private GetNewId(): number {
        this.ReadDB();
        const id = this.storageObject.idgen++;
        this.WriteDB();
        return id;
    }
}

export default LocalDB;
