// Storage and Definition for Trackable objects
// A "Trackable" is something we keep track of,
// i.e.: "Soreness" or "Tiredness" or "Worry".

import LocalDB from './LocalDB';

export interface ITrackable {
    id?: number;
    name: string;
}

export class Trackable implements ITrackable {
    id?: number;
    name: string;
}

export class TrackableDB {
    private _db: LocalDB = null;

    constructor() {
        this._db = new LocalDB('trackables');
    }

    get(id: number): ITrackable {
        return this._db.GetByID(id) as ITrackable;
    }

    getAll(): ITrackable[] {
        return this._db.Get() as ITrackable[];
    }

    add(trackable: ITrackable): number {
        let res = this._db.Insert(trackable);
        return res?.id;
    }

    remove(trackable: ITrackable): boolean {
        return this._db.Delete(trackable.id);
    }

    /// Saves a trackable to the DB. Updates
    /// existing entries or adds a new one.
    save(trackable: Trackable): boolean {
        if (trackable.id) {
            let row = {
                ...trackable,
                id: trackable.id,
            };

            return this._db.Update(row);
        } else {
            // Doesn't exist, add a new one
            return this.add(trackable) >= 0;
        }
    }
}
