import React from 'react';
import ReactDOM from 'react-dom';
import { BrowserRouter } from 'react-router-dom';
import HomePage from './pages/HomePage';
import { Exception } from './System';

const appNode = document.getElementById('feelix-root');

if (appNode) {
    ReactDOM.render(
        <BrowserRouter>
            <HomePage />
        </BrowserRouter>,
        appNode
    );
} else {
    throw new Exception("DOM node with id 'feelix-root' not found!");
}
